import React from 'react';
import {csv} from 'd3-fetch';
import SankeyHint from './../Components/SankeyHint';
import Flux2014 from './../data/df_flux14_pop.csv';

import {
	generateSankey,
	isPSN,
	arraySum,
	generateCheckBox
} from '../utils';

import {
	sankey_chartprops,
	smallSaneky_chartprops
} from '../constants';

import {format} from 'd3-format';
const perc = format(".2p");
const number = format(",.0f")	;

function title1(title){
	
	return (
    		<div className="Chart-header">
				<h1> {title} </h1> 
			</div>
    	);
}

function title2(title){
	
	return (
    		<div className="Chart-header">
				<h2> {title} </h2> 
			</div>
    	);
}

function renderCheckboxes(id) {
	const checkbox = this.state.checkboxes[id];
	return (
			<div>
				<label className="switch">
					<input 
						type="checkbox"
						checked={checkbox.checked}
            onChange={toggleCheckbox.bind(this, id)}
					/>
					<span className="slider"></span>
				</label>
			</div>
	);
}

function toggleCheckbox(index) {

			const checkboxes = this.state.checkboxes;
			checkboxes[index].checked = !checkboxes[index].checked;
			this.setState({
				checkboxes: checkboxes
			});
	}

export default class SankeyFluxPSN extends React.Component {

	constructor(props) {
			super(props)
			this.state = {
					data_horsPSN: { nodes: [], links: [] },
					data_dansPSN: { nodes: [], links: [] },
					data_versPSN: { nodes: [], links: [] },
					data_depuisPSN: { nodes: [], links: [] },
					sumMigration : 0,
					checkboxes: generateCheckBox(['Vers PSN', 'Depuis PSN','Au sein de PSN'])
			};
  }

	componentDidMount() {

		csv(Flux2014)
				.then(data => {
					const horsPSN = generateSankey(data.filter( d => (!isPSN(d.code_insee2)) && (!isPSN(d.code_insee1))) );
					const dansPSN = generateSankey(data.filter( d => (isPSN(d.code_insee2)) && (isPSN(d.code_insee1))) );
					const versPSN = generateSankey(data.filter( d => (isPSN(d.code_insee2)) && (!isPSN(d.code_insee1))) );
					const depuisPSN = generateSankey(data.filter( d => (!isPSN(d.code_insee2)) && (isPSN(d.code_insee1))) );
		
					this.setState({
						data_horsPSN: horsPSN,
						data_dansPSN: dansPSN,
						data_versPSN: versPSN,
						data_depuisPSN: depuisPSN,
						sumMigration: arraySum(data,'migration')
					});
					
			});
	}

	render() {

		return ( 
			<div>
			
			{title1("Flux migratoires: nombre de déménagements en 2014")}

				<div className='insideChartWrapperHalf'>
					<div>
						<div className="checkbox-container">
							{renderCheckboxes.call(this,0)}
						</div>
						{title2("Vers la Vallée de Seine")}
					</div>
					<p> { number(arraySum(this.state.data_versPSN.links, 'value'))} déménagements, soit { perc(arraySum(this.state.data_versPSN.links, 'value') / this.state.sumMigration)} des déménagements en France en 2014 </p>

					<SankeyHint style={{ labels: {fontSize:'11px'} }}
						data={this.state.data_versPSN} 
						chartprops={smallSaneky_chartprops} 
					/>
				</div>

			
				<div className='insideChartWrapperHalf'>
					<div>
						<div className="checkbox-container">
							{renderCheckboxes.call(this,1)}
						</div>
						{title2("Depuis la Vallée de Seine")}
					</div>
					<p> { number(arraySum(this.state.data_depuisPSN.links, 'value'))} déménagements, soit { perc(arraySum(this.state.data_depuisPSN.links, 'value') / this.state.sumMigration)} des déménagements en France en 2014 </p>

					<SankeyHint style={{ labels: {fontSize:'11px'} }}
						data={this.state.data_depuisPSN} 
						chartprops={smallSaneky_chartprops} 
					/>
				</div>
				
				<div className='insideChartWrapper'>
					<div>
						<div className="checkbox-container">
							{renderCheckboxes.call(this,2)}
						</div>
						{title2("Au sein de la Vallée de Seine")}
					</div>
					<p> { number(arraySum(this.state.data_dansPSN.links, 'value'))} déménagements, soit { perc(arraySum(this.state.data_dansPSN.links, 'value') / this.state.sumMigration)} des déménagements en France en 2014 </p>
					<SankeyHint style={{ labels: {fontSize:'12px'} }}
						data={this.state.data_dansPSN} 
						chartprops={sankey_chartprops} 
					/>
				</div>
			
			</div>
			
		);
	}
	
}


